import React, { useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import Input from '../../components/Input/Input';
import css from './Enter.module.css';
import Back from '../../components/Back/Back';
import request from '../../request';
import Modal from '../../components/Modal/Modal';
import Button from '../../components/Button/Button';
import Isemail from 'isemail';

const RecoverPWD = () => {
  const [title, setTitle] = useState('');
  const [email, setEmail] = useState('');
  const [success, setsuccess] = useState(false);
  const [openModal, setOpenModal] = useState(false);
  const [textError, setTextError] = useState('');

  if (success) return <Redirect to='/login' />;

  return (
    <div className={css.base_container}>
      <Back to='/login' />
      <div className={css.header}>cc </div>
      <div className={css.content}>
        <div className={css.label}>
          <label>  E-mail</label>
        </div>
        <div className={css.formInput}>
          <Input
            enterInput
            type='text'
            name='login'
            value={email}
            setValue={(v) => {
              if (!/[-]/gi.test(v)) {
                setEmail(v);
              }
            }}
            valid={Isemail.validate(email, { minDomainAtoms: 2 })}
          />
        </div>
        <Button
          className={css.nextBtn}
          disabled={!Isemail.validate(email, { minDomainAtoms: 2 })}
          onclick={async () => {
            try {
              const data = await request.post('persident/pwdrecover', [
                ['email', email],
              ]);
              if (data.status !== 'error') {
                setOpenModal(true);
                setTitle('');
                setTextError('      ');
                setEmail('');
              } else {
                setOpenModal(true);
                setTextError(data.errordesc);
                setTitle('');
              }
            } catch (error) {
              setOpenModal(true);
              setTextError(error);
              setTitle('');
            }
          }}
        >
          
        </Button>
      </div>
      <div className={css.regBtn}>
        <label className={css.passLink2}>  ?</label>
        <Link to='/registration' className={css.registration}>
          
        </Link>
      </div>
      <Modal
        open={openModal}
        close={() => setOpenModal(false)}
        title={title}
        text={textError}
        BtnClick={() => {
          setOpenModal(false);
          setsuccess(true);
        }}
        buttonTitle=''
      />
    </div>
  );
};

export default RecoverPWD;
